export const localStore = {
    set: (key, value) => {
        return localStorage.setItem(key, value);
    },
    get: (key, defaultShizzle) => {
        const lsShizzle = localStorage.getItem(key);
        return JSON.parse(lsShizzle);
    },
    remove: (key) => {
        return localStorage.removeItem(key);
    },
};
